import React, { Component } from "react";
import ParticlesBg from "particles-bg";
import Fade from "react-reveal";

class Header extends Component {
  render() {
    if (!this.props.data) return null;

    const linkedin = this.props.data.linkedin;
    const github = this.props.data.github;
    const resume = this.props.data.resume;
    const name = this.props.data.name;
    const description = this.props.data.description;

    return (
      <header id="home">
        <ParticlesBg color="#581845" num={300} type="cobweb"  bg={true} />

        <nav id="nav-wrap">
          <a className="mobile-btn" href="#nav-wrap" title="Show navigation">
            Show navigation
          </a>
          <a className="mobile-btn" href="#home" title="Hide navigation">
            Hide navigation
          </a>

          <ul id="nav" className="nav">
            <li className="current">
              <a className="smoothscroll" href="#home">Home</a>
            </li>

            <li>
              <a className="smoothscroll" href="#about">About</a>
            </li>

            <li>
              <a className="smoothscroll" href="#resume">Technologies</a>
            </li>

            <li>
              <a className="smoothscroll" href="#portfolio">Works</a>
            </li>

            <li>
              <a className="smoothscroll" href="#contact">Contact</a>
            </li>
          </ul>
        </nav>

        <div className="row banner">
          <div className="banner-text">
            <Fade bottom>
              <h1 className="responsive-headline">{name}</h1>
            </Fade>
            <Fade bottom duration={700}>
              <h2 class="h2-description">{description}.</h2>
            </Fade>
            <hr />
            <Fade bottom duration={1200}>
              <ul className="social">
                <a href={linkedin} className="button btn linkedin-btn">
                  <i className="fa fa-book"></i>Linkedin
                </a>
                <a href={github} className="button btn github-btn">
                  <i className="fa fa-github"></i>Github
                </a> &nbsp; &nbsp; &nbsp; &nbsp;
                <a href={resume} className="button btn resume-btn">
                      <i className="fa fa-download"></i>Resume
                    </a>
              </ul>
            </Fade>
          </div>
        </div>

        <p className="scrolldown">
          <a className="smoothscroll" href="#about">
            <i className="icon-down-circle"></i>
          </a>
        </p>
      </header>
    );
  }
}

export default Header;
